<template>
  <t-table
    class="list-wrapper"
    :isLoading='isLoading'
    :data='data'
    :openFilter="true"
    :pagination='pagination'
    :sizeChange="handleSizeChange"
    :currentChange="handleCurrentChange"
  >
    <template #filter>
      <el-button class="mgr-24" type="primary" size="small" @click='createItem'>
        
      </el-button>
      <span class="space"></span>
      <el-input class="input-w-300" size="small" type='text' v-model='params.keyword' @keydown.enter.native="handleSearch">
        <el-button size="small" type="primary" slot="append" icon="el-icon-search" v-t-throttle='searchOptions'></el-button>
      </el-input>
    </template>
    <template #table>
      <el-table-column prop="desc" label="" align="center" />
      <el-table-column label="" align="center" width='250'>
        <template slot-scope="scope">
          <span>{{ scope.row.createdTime | t-time-format }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="120">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click='handleDelete(scope.row._id)'></el-button>
        </template>
      </el-table-column>
    </template>
  </t-table>
</template>

<script>
import { nextTodoList, createNextTodoList, deleteNextTodoList } from '@/apis/apis'
export default {
  name: 't-next-to-do',
  components: {},
  props: {},
  data () {
    return {
      isLoading: false,
      data: null,
      params: {
        page: 1,
        size: 20,
        keyword: null
      },
      pagination: {},
      searchOptions: {
        type: 'onclick',
        fn: this.handleSearch,
        params: []
      }
    }
  },
  computed: {},
  methods: {
    getList () {
      this.isLoading = true
      return new Promise((resolve) => {
        nextTodoList(this.params)
          .then((response) => {
            setTimeout(() => {
              const { result, code } = response.data
              if (code === 0) {
                const { list, total } = result
                this.data = list
                this.pagination.total = total
                this.pagination.page = this.params.page
                this.pagination.size = this.params.size
                this.isLoading = false
              }
            }, this.$store.state.apiDelay)
          })
      })
    },
    //  size 
    handleSizeChange (val) {
      this.params.size = val
      /**
       *  size  page 
       *  page 
       *  page 
       *
       *  ElementUI  size  page  size  page 
       *  this.params.page this.params.size 
       */
      if (
        this.pagination.total &&
        Math.ceil(this.pagination.total / val) >= this.params.page
      ) {
        this.getList()
      }
    },
    //  page 
    handleCurrentChange (val) {
      this.params.page = val
      this.getList()
    },
    handleSearch () {
      this.getList()
    },
    createItem () {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^.{1,50}$/,
        inputErrorMessage: '150'
      }).then(({ value }) => {
        createNextTodoList({ desc: value })
          .then((resposne) => {
            const { code, msg } = resposne.data
            if (code === 0) {
              this.$message.success(msg)
              this.getList()
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    handleDelete (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteNextTodoList({ id })
          .then((response) => {
            const { code, msg } = response.data
            if (code === 0) {
              this.$message.success(msg)
              this.getList()
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  created () {
    this.getList()
  },
  mounted () {},
  watch: {}
}
</script>

<style lang="scss" scoped>
.list-wrapper {
  width: 100%;
  height: 100%;
  .space {
    flex: 1;
  }
}
</style>
